          render: (h, params) => {
            const row = params.row
            return h('span', {
              style: {
                color: '#2d8cf0',
                cursor: 'pointer'
              },
              on: {
                click: () => {
                  this.showInfo(row)
                }
              }
            }, row.projectName);
          }
        },
        {
          title: '总投资额（万元）',
          key: 'auditBatchVolumeSum'
        },
        {
          align: 'center',
          title: '审核状态',
          key: 'action',
          fixed: 'right',
          width: 120,
          render: (h, params) => {
            const row = params.row
            const status = row.auditStatus
            const audit = this.status[status]
            const buttons = [
              h('Button', {
                style: {
                  color: audit.color + '!important'
                },
                props: {
                  type: 'text',
                  size: 'small'
                },
                on: {
                  click: () => {
                    const auditTime = row.auditTime ? dateformat(row.auditTime, 'YYYY年MM月DD日 HH:mm:ss') : ''
                    if (status === 2 || status === 1) {
                      // 赋值
                      this.auditForm = {
                        // 审核人
                        auditMan: row.auditMan,
                        // 审核结果
                        result: audit,
                        auditTime: auditTime,
                        // 审核意见
                        auditMessage: row.auditMessage
                      }
                      this.$refs.audit.displayAudit()
                    } else {
                      this.showCheck(row)
                    }
                  }
                }
              }, audit.name)
            ]
            // 如果是审核驳回的话，多添加一个按钮，再次提交
            if (status === 2) {
              buttons.push(h('Button', {
                style: {
                  color: 'green'
                },
                props: {
                  type: 'text',
                  size: 'small'
                },
                on: {
                  click: () => {
                    this.showCheck(row)
                  }
                }
              }, '再次提交'))
            }
            return h('div', buttons);
          }
