  computed: {
    totalVolume() {
      let total = 0
      this.totalList.forEach(item => {
        total = calcu.accAdd(total, item.auditSubentryVolume)
      })
      return total;
    }
  },
  mounted() {
    this.getSubentryProject();
    var myDate = new Date();
    const time = myDate.toLocaleString('chinese', { hour12: false });
    this.curTime = time.substring(0, 15)
  },
  methods: {
    // 新增表格内容
    addTableInfo() {
      this.isbtnShow = true
      this.isCreate = true;
      const uploadFile = this.$refs.uploadFile
      if (uploadFile) {
        uploadFile.clearFileItems()
      }
      this.selTypeMod = true;
    },
    modifyData() {
      const that = this
      if (!that.isSelected) {
        that.$Message.warning('您未选择需要修改的内容');
        return
      }
      const row = Object.assign({}, that.currentRow)
      const auditStatus = row.auditStatus
      // 未审核 和 审核驳回可以更改信息
      const flag = [2, 3].indexOf(auditStatus) !== -1
      const subentryTypes = row.subentryTypes
      const showTip = subentryTypes.indexOf('6') === -1
      that.isCreate = false;
      that.$nextTick(() => {
        const name = 'uploadFile' + (showTip ? '1' : '2')
        that.$refs[name].getFileItems()
      })
      that.uploadFile.fileids = row.subentryFileIds
      that.isbtnShow = flag
      that.showTip = showTip
      that.confirmSel()
    },
    // 弹出框按钮
    handleSubmit(index) {
      const that = this
      that.$refs['formValidate' + index].validate((valid) => {
        if (valid) {
          that.form.auditSubentryType = that.tabIndex
          that.commonUtil.ajaxPostUtil('/auditSubentry/addAuditSubentry', that.form, (res) => {
            // 关闭数据加载
            that.$Spin.hide();
            if (res.code === 1) {
              that.$Message.success(res.msg);
              // 关闭弹窗，刷新列表
              that.refreshTypeList();
            } else {
              that.$Message.error(res.msg);
            }
          }, (error) => {
            that.$Message.error('网络连接错误');
            // 关闭数据加载
            that.$Spin.hide();
          }, that)
        }
      })
    },
    getUnitName(unit, auditSubentryType) {
      const tabIndex = this.tabIndex
      if (unit === null) return ''
      const unitArray = this.getUnitArray(tabIndex === 0 ? auditSubentryType : tabIndex)
      return unit ? unitArray[unit].label : ''
    },
    getUnitArray(index) {
      return index === 2 ? this.equipUnit : this.projectUnit
    },
    // 刷新相对应得列表
    refreshTypeList() {
      this.editIndex = -1;
      this.getSubentryByType(this.tabIndex, false)
    },
    // 添加按钮
    showAddModel(index, flag) {
      this.$refs['formValidate' + index].resetFields();
      switch (index) {
        case 1:
          this.addModel1 = flag;
          break;
        case 2:
          this.addModel2 = flag;
          break;
        case 3:
          this.addModel3 = flag;
          break;
      }
    },
    outChange(val) {
      this.showTip = !this.showTip;
    },
    // 查询到投资汇总表
    getTotalList(type) {
      this.getSubentryByType(type, false)
    },
    // 确定选择
    confirmSel() {
      this.currentTab = 'name0'
      if (this.showTip) {
        // 基建经费支出、编辑模式查全部
        this.clearAllData()
        this.getTotalList(0)// 全部
        this.infoModal = true;
        this.selTypeMod = false;
      } else {
        // 其他经费支出、编辑模式查全部
        this.tabIndex = 6
        this.getTotalList(6)// 其他经费支出
        this.otherModal = true;
        this.selTypeMod = false;
      }
    },
    // 弹框表格
    handleEdit(row, index) {
      this.editIndex = index;
      this.editForm = Object.assign({}, row)
    },
    // 删除行
    handleDelete(id) {
      const that = this;
      that.$Modal.confirm({
        title: '删除',
        content: '确认删除此数据吗？',
        onOk: () => {
          that.$Spin.show({
            render: (h) => {
              return h('div', [
                h('Icon', {
                  'class': 'demo-spin-icon-load',
                  props: {
                    type: 'ios-loading',
                    size: 18
                  }
                }),
                h('div', '加载中')
              ])
            }
          });
          that.commonUtil.ajaxDeleteUtil('/auditSubentry/deleteAuditSubentry/' + this.tabIndex + '/' + id, (res) => {
            // 关闭数据加载
            that.$Spin.hide();
            if (res.code === 1) {
              that.$Message.success(res.msg);
              that.refreshTypeList()
            } else {
              that.$Message.error(res.msg);
            }
          }, (error) => {
            this.$Message.error('网络连接错误');
            this.$Spin.hide();
          }, that)
        }
      });
    },
    handleSave() {
      const that = this;
      that.$Spin.show({
        render: (h) => {
          return h('div', [
            h('Icon', {
              'class': 'demo-spin-icon-load',
              props: {
                type: 'ios-loading',
                size: 18
              }
            }),
            h('div', '修改数据中')
          ])
        }
      });
      that.commonUtil.ajaxPutUtil('/auditSubentry/updateAuditSubentry', that.editForm, (res) => {
        // 关闭数据加载
        that.$Spin.hide();
        if (res.code === 1) {
          that.$Message.success(res.msg);
          that.refreshTypeList()
        } else {
          that.$Message.error(res.msg);
        }
      }, (error) => {
        that.$Message.error('网络连接错误');
        // 关闭数据加载
        that.$Spin.hide();
      }, that)
    },
    // 打开详情弹出来
    showInfo(row) {
      // "添加“按钮隐藏
      this.isbtnShow = false;
      const subentryTypes = row.subentryTypes
      const showTip = subentryTypes.indexOf('6') === -1
      this.showTip = showTip
      this.uploadFile.fileids = row.subentryFileIds
      this.$nextTick(() => {
        const name = 'uploadFile' + (showTip ? '1' : '2')
        this.$refs[name].getFileItems()
      })
      this.showTip = subentryTypes.indexOf(6) === -1
      this.confirmSel()
    },
    // 取消
    cancelTable() {
      if (this.showTip) {
        this.infoModal = false
      } else {
        this.otherModal = false
      }
      this.getSubentryProject()
    },
    confirmTable() {
      const that = this;
      const flag = that.showTip
      const fileIds = that.$refs['uploadFile' + (flag ? '1' : '2')].getFileIds()
      that.$Spin.show();
      that.commonUtil.ajaxPutUtil('/auditSubentry/updateFileIdsByProjectId', { fileIds: fileIds }, (res) => {
        // 关闭数据加载
        that.$Spin.hide();
        if (res.code === 1) {
          that.$Message.success(res.msg);
          if (flag) {
            that.infoModal = false
          } else {
            that.otherModal = false
          }
          that.getSubentryProject()
        } else {
          that.$Message.error(res.msg);
        }
      }, (error) => {
        that.$Message.error('网络连接错误');
        // 关闭数据加载
        that.$Spin.hide();
      }, that)
    },
    clearAllData() {
      this.constructionList = []
      this.equipList = []
      this.otherProjectList = []
      this.otherCostList = []
      this.reserveList = []
      this.otherList = []
      this.totalList = []
      this.infoModal = false
    },
    showCheck(row) {
      const that = this;
      if (row.auditStatus === 1) {
        that.$Message.error('项目已经审核通过，无需继续提交');
        return;
      }
      that.$Modal.confirm({
        title: '是否确认提交审核',
        content: '',
        onOk: () => {
          that.$Spin.show({
            render: (h) => {
              return h('div', [
                h('Icon', {
                  'class': 'demo-spin-icon-load',
                  props: {
                    type: 'ios-loading',
                    size: 18
                  }
                }),
                h('div', '加载中')
              ])
            }
          });
          that.commonUtil.ajaxPutUtil('/auditBatch/updateAuditBatch', { auditBatchId: row.auditBatchId, auditStatus: 0 }, (res) => {
            // 关闭数据加载
            that.$Spin.hide();
            if (res.code === 1) {
              that.$Message.success(res.msg);
              that.getSubentryProject()
            } else {
              that.$Message.error(res.msg);
            }
          }, (error) => {
            that.$Message.error('网络连接错误');
            // 关闭数据加载
            that.$Spin.hide();
          }, that)
        }
      });
    },
    // 选中一行进行修改数据
    selectRow(currentRow) {
      this.isSelected = true;
      this.currentRow = currentRow;
    },
    // 删除行记录
    deleteData() {
      const that = this;
      const row = that.currentRow
      if (!that.isSelected) {
        that.$Message.warning('您未选择需要删除的内容');
        return
      }
      if ([2, 3].indexOf(row.auditStatus) === -1) {
        that.$Message.warning('该审核状态下不能删除数据');
        return
      }
      that.$Modal.confirm({
        title: '删除',
        content: '确认删除此数据吗？',
        onOk: () => {
          that.$Spin.show({
            render: (h) => {
              return h('div', [
                h('Icon', {
                  'class': 'demo-spin-icon-load',
                  props: {
                    type: 'ios-loading',
                    size: 18
                  }
                }),
                h('div', '加载中')
              ])
            }
          });
          that.commonUtil.ajaxDeleteUtil('/auditSubentry/deleteAuditSubentry/0/' + row.relationId, (res) => {
            // 关闭数据加载
            that.$Spin.hide();
            if (res.code === 1) {
              that.$Message.success(res.msg);
              that.getSubentryProject();
            } else {
              that.$Message.error(res.msg);
            }
          }, (error) => {
            this.$Message.error('网络连接错误');
            this.$Spin.hide();
          }, that)
        }
      });
    },
    // 切换tab
    switchTab(name) {
      const nameArray = ['name0', 'name1', 'name2', 'name3', 'name4', 'name5']
      const index = nameArray.indexOf(name)
      if (index !== -1) {
        this.tabIndex = index
        this.currentTab = name
        this.editIndex = -1
        this.getSubentryByType(index, true)
      }
    },
    // 通过type 赋值 list
    setListByType(index, data) {
      switch (index) {
        case 0:
          this.totalList = data
          break;
        case 1:
          this.constructionList = data
          break;
        case 2:
          this.equipList = data
          break;
        case 3:
          this.otherProjectList = data
          break;
        case 4:
          this.otherCostList = data
          break;
        case 5:
          this.reserveList = data
          break;
        case 6:
          this.otherList = data
          break;
      }
    },
    // 通过type查询投资汇总
    getSubentryByType(type, flag) {
      const that = this;
      that.$Spin.show({
        render: (h) => {
          return h('div', [
            h('Icon', {
              'class': 'demo-spin-icon-load',
              props: {
                type: 'ios-loading',
                size: 18
              }
            }),
            h('div', '加载中')
          ])
        }
      });
      that.commonUtil.ajaxPostUtil('/auditSubentry/searchAuditSubentry', { auditSubentryType: type }, (res) => {
        // 关闭数据加载
        that.$Spin.hide();
        if (res.code === 1) {
          let tabFlag = 1
          that.setListByType(type, res.data)
          tabFlag = (type === 1 || type === 3 || type === 6 ? 1 : (type === 2 ? 2 : 3))
          that.showAddModel(tabFlag, false)
        } else {
          that.$Message.error(res.msg);
        }
      }, (error) => {
        that.$Message.error('网络连接错误');
        // 关闭数据加载
        that.$Spin.hide();
      }, that)
    },
    getSubentryProject() {
      const that = this
      that.currentRow = {}
      that.isSelected = false;
      that.$Spin.show({
        render: (h) => {
          return h('div', [
            h('Icon', {
              'class': 'demo-spin-icon-load',
              props: {
                type: 'ios-loading',
                size: 18
              }
            }),
            h('div', '加载中')
          ])
        }
      });
      that.commonUtil.ajaxPostUtil('/project/searchProject', { auditBatchType: 1 }, (res) => {
        // 关闭数据加载
        that.$Spin.hide();
        if (res.code === 1) {
          const data = res.data
          if (data) {
            that.projectList = data
            that.total = data.length
          }
        } else {
          that.$Message.error(res.msg);
        }
      }, (error) => {
        that.$Message.error('网络连接错误');
        // 关闭数据加载
        that.$Spin.hide();
      }, that)
      that.commonUtil.getAuditCountApi(that)
    }
  }
}
</script>



